<template>
  <div class="header">
    <ul class="header-button-left">
      <li v-if="step == 1" @click="step--">취소</li>
      <li v-if="step == 2" @click="step--">back</li>
    </ul>
    <ul class="header-button-right">
      <!-- <li v-if="step == 0" @click="step++">Next</li> -->
      <li v-if="step == 1" @click="step++">Next</li>
      <li v-if="step == 2" @click="publish">발행</li>
    </ul>
    <img src="./assets/logo.png" class="logo" alt="logo" />
  </div>
  <div v-if="step == 0">
    <h4>안녕하세요 {{ $store.state.name }}님</h4>
    <button @click="$store.commit('이름변경')" class="chid">
      {{ $store.state.chid }}로 변경
    </button>
  </div>
  <!-- <button @click="$store.commit('plus')">age</button> -->
  <Container
    :imageS="imageS"
    :post="post"
    :step="step"
    @write="작성한글 = $event"
  />

  <button @click="more" class="morebtn" v-if="step == 0">더보기</button>

  <div class="footer">
    <ul class="footer-button-plus">
      <input
        @change="upload"
        multiple
        accept="image/*"
        type="file"
        id="file"
        class="inputfile"
      />
    </ul>
    <button @click="step = 0">HOME</button>
    <label for="file" class="input-plus" @click="step = 1"
      ><img src="./assets/icon-pen.png" width="25" alt="게시물작성"
    /></label>
    <!-- <button @click="step = 1">
      <img src="./assets/icon-photo-gallery.png" alt="사진선택" />
    </button> -->
    <!-- <button @click="step = 2">
      <img src="./assets/icon-pen.png" width="25" alt="내용작성" />
    </button> -->
  </div>

  <div v-if="step == 0">내용 0</div>
  <div v-if="step == 1" class="selectphoto">사진 선택</div>
  <div v-if="step == 2" class="writetxt">글을 작성 해 주세요</div>
</template>
<script>
import Container from "./components/Container.vue";
import postdata from "./assets/postdata.js";
import axios from "axios";

export default {
  name: "App",
  data() {
    return {
      post: postdata,
      더보기: 0,
      step: 0,
      imageS: "",
      작성한글: "", // 유저가 작성 할 글공간 비우기
    };
  },
  components: {
    Container: Container,
  },
  methods: {
    more() {
      axios
        .get(`https://jellyzoo.github.io/data/vuemore${this.더보기}.json`)
        .then((결과) => {
          this.post.push(결과.data);
          this.$store.commit("changePosts", this.post);
          this.더보기++;
        });
    },
    upload(e) {
      const 파일 = e.target.files; // 업로드된 모든 파일들이 담겨있음.
      console.log(파일[0]); // 업로드한 파일을 콘솔에서 출력
      const url = URL.createObjectURL(파일[0]); // 함수를 이용해 URL생성
      console.log(url);
      this.imageS = url; // imageS라는 변수에 url을 담아
      this.step++;
    },
    publish() {
      const 내게시물 = {
        name: "ggunmanduu",
        userImage:
          "https://i.pinimg.com/236x/f3/dc/43/f3dc43e3a231b4d7e3fb545ef87acb1a.jpg",
        postImage: this.imageS, // 내가 업로드 한 이미지 url
        likes: 0,
        date: "May 15",
        liked: false,
        content: this.작성한글, // 내가 입력한 글
        filter: "perpetua",
      };
      this.post.unshift(내게시물);
      this.step = 0;
    },
  },
};
</script>

<style>
@import "./css/style.css";
</style>
