<template>
  <div>
    <!-- 게시물 갯수만큼 반복 : step 0이면 보이는 영역-->
    <div v-if="step == 0">
      <Post :post="post[i]" v-for="(a, i) in post" :key="i" :idx="i" />
    </div>

    <!-- 필터 선택 페이지 : step 1이면 보이는 영역-->
    <div v-if="step == 1">
      <div
        class="upload-image"
        :style="`background-image:url(${imageS})`"
      ></div>
      <div class="filters">
        <div class="filter-1"></div>
        <div class="filter-1"></div>
        <div class="filter-1"></div>
        <div class="filter-1"></div>
        <div class="filter-1"></div>
      </div>
    </div>

    <!-- 글작성 업로드 페이지 : step 2이면 보이는 영역-->
    <div v-if="step == 2">
      <div
        class="upload-image"
        :style="`background-image:url(${imageS})`"
      ></div>
      <div class="write">
        <textarea
          class="write-box"
          @input="$emit('write', $event.target.value)"
        ></textarea>
        <!-- 자식요소에서 부모요소로 emit -->
      </div>
    </div>
  </div>
</template>

<script>
import Post from "./Post.vue";
export default {
  name: "Container",
  data() {
    return {};
  },
  components: {
    Post: Post,
  },
  props: {
    post: Array,
    step: Number,
    imageS: String,
  },
};
</script>

<style>
div {
  background: #2b2b2b;
  color: #ffffff;
}
.upload-image {
  width: 100%;
  height: 450px;
  background: rgb(177, 177, 177);
  background-size: cover;
}
.filters {
  overflow-x: scroll;
  white-space: nowrap;
}
.filter-1 {
  width: 100px;
  height: 100px;
  background: rgb(177, 177, 177);

  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: white;
  background-size: cover;
}
.write-box {
  border: none;
  width: 90%;
  height: 100px;
  padding: 15px;
  margin: 20px auto;
  display: block;
  outline: none;
  border-radius: 10px;
  color: #ffffff;
  background-color: #4e4e4e;
}
</style>
