import { createStore } from 'vuex'
import postdata from '@/assets/postdata'
export default createStore({
  state () {
    // 데이터 저장소
    return {
      postdata: postdata,
      name: '김지우',
      age: 27,
      likes: postdata.map((post) => post.likes),
      chid: '아이디'
    }
  },
  getters: {
  },
  mutations: {
    // 데이터 수정방법을 석택
    이름변경 (state) {
      state.name = 'ggunmanduu'
      state.chid = '이름으'
    },

    plus (state) {
      state.age++
    },
    like (state, idx) {
      state.likes[idx]++
    },
    changePosts (state, posts) {
      state.posts = posts
      state.likes = posts.map((post) => post.likes)
    }
  },

  actions: {
  },
  modules: {
  }
})
